import $ from 'jquery';
import { useEffect, useState } from "react"
import { useDispatch, useSelector } from 'react-redux';
import { setting } from './../store/store.js';


function Map(){
  let state = useSelector(state => state);
  let dispatch = useDispatch();
  let [theDay, setTheDay] = useState();
  let [infoArr, setInfoArr] = useState();

  useEffect(() => {
    let getItem = localStorage.getItem('place');
    getItem = JSON.parse(getItem);
    dispatch(setting(getItem));
  }, [])

  useEffect(() => {
    if(theDay !== undefined){
      theDay.map((value, i) => {
        let findValue = state.flag.find(element => element.name === value.location);
        $('.map-flag').eq(i).css('top', `${findValue.top}%`);
        $('.map-flag').eq(i).css('left', `${findValue.left}%`);
      })
    } else {
      return
    }
  }, [theDay])

  useEffect(() => {
    window.scrollTo({
      top: 150,
      left: 0,
      behavior: "smooth"
    });
  },[infoArr])

  return (
    <div className="map-container">
      <h3>Map</h3>
      <div className='map-input-container'>
        <label htmlFor='input-schedule'>
          Day
          <input type="number" id='input-schedule'></input>
        </label>
        <button className='map-search-btn' onClick={() => {
          setTheDay(state.days[$('#input-schedule').val()]);
        }}>
          <i className="ri-search-2-fill"></i>
        </button>
      </div>
      <div className="map-main-container">
        <div className='map-bg'>
          {
            theDay !== undefined
            ? theDay.map((value, i) => {
              return(
                <span className="map-flag" key={i} onClick={() => {
                  let arr = [];
                  theDay.map((element, i) => {
                    if(value.location === element.location){
                      arr.push(element);
                    }
                  });
                  let copy = [...arr];
                  setInfoArr(copy);
                }}>
                  <i className="ri-flag-fill"></i>
                </span>
              )
            })
            : null
          }
        </div>
      </div>
      <div className='map-info-container'>
        {
          infoArr !== undefined
          ? infoArr.map((value ,i) => {
            return(
              <div className='flag-info' key={i}>
                <h4>
                  <i className="ri-map-pin-fill"></i>
                  {value.locationName}
                </h4>
                <ul className='info-ul'>
                  <li>{`${value.time[0]} : ${value.time[1]} ~ ${value.time[2]} : ${value.time[3]}`}</li>
                  <li>{value.location}</li>
                  <li>{value.locationCategory}</li>
                  <li>Day {value.day}</li>
                </ul>
              </div>
            )
          })
          : null
        }
      </div>
    </div>
  )
}


export {Map}